import React from 'react';

function HomeHero() {
  return (
    <div>
      <div className="hero">
        <div className="flex">
          <div className="flex-item">
            <div className="container">
              <div className="face">
              </div>
              <div className="hero-details">
                <h1>Hello, I'm Ryan.</h1>
                <p>A passionate coder and pixel maker with experience taking projects from concept to completion, and leading small cross-functional teams through the agile development process.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomeHero;


